import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
// We import the authentication provider to test the log-out function.
import { AuthProvider } from '../../providers/auth/auth';
import firebase from 'firebase';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})

export class HomePage {
	public items: Array<any> = [];
	public itemRef: firebase.database.Reference = firebase.database().ref('/items');
	
	constructor(public navCtrl: NavController, public authProvider: AuthProvider) { 
	}
		

	ionViewDidLoad() {
		this.itemRef.on('value', itemSnapshot => {
			this.items = [];
			itemSnapshot.forEach( itemSnap => {
				this.items.push(itemSnap.val());
				return false;
			});
		});
	}

	addItem(name: string): void {
		const itemRef: firebase.database.Reference = firebase.database().ref(`/items/`);
		this.itemRef.update({name});
	}

	/*
removeItem(itemId: string): void {
  this.itemRef.remove(itemId);
} */
  
	logMeOut() {
		this.authProvider.logoutUser().then( () => {
		this.navCtrl.setRoot('LoginPage');
		});
	}

}



